<template>
	<div class="diagnosisTable dataTable">
		<div v-if="isLoading" class="loading">
			<i class="loading-i fa fa-spinner fa-pulse"></i>
			<span v-if="isNetErr">获取数据失败，请检查网络是否异常</span>
			<span v-else>正在加载中</span>
		</div>
		<div v-else>
			<div class="search">
				<div>
					<el-input type="text" v-model="search_val" placeholder="请输入病理号" style="width: 200px;"></el-input>
					<el-button type="primary" @click="search"><i class="fa fa-search"></i>查询</el-button>
					
				</div>
				<div class="search-right">
					<el-button type="primary" @click="download">导出</el-button>
					<el-upload
						:action="uploadUrl"
						:show-file-list="false"
						:on-success="success_upload"
						:on-error="error_upload"
						:before-upload="before_upload"
						>
						<el-button type="primary">{{isUpload?'正在导入中...':'导入'}}<i class="fa fa-spinner fa-pulse" v-if="isUpload"></i></el-button>
					</el-upload>
				</div>
			</div>
			<el-table
			    :data="tableData"
			    style="width: 100%"
			    :max-height="table_height">
		    <el-table-column
		      type="selection"
		      width="55">
		    </el-table-column>
		    <el-table-column
		      fixed
		      prop="pathology"
		      label="病理号"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="diagnosis_label_lastest"
		      label="最新诊断标签"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="his_diagnosis_label1"
		      label="历史诊断标签1"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="his_diagnosis_label2"
		      label="历史诊断标签2"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="his_diagnosis_label3"
		      label="历史诊断标签3"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="his_diagnosis_label4"
		      label="历史诊断标签4"
		      >
		    </el-table-column>
		    <el-table-column
		      fixed="right"
		      label="操作"
		      >
		      <template slot-scope="scope">
		        <el-button
		          @click.native.prevent="updateRow(scope.$index, tableData)"
		          type="text"
		          size="small">
		          更新
		        </el-button>
		      </template>
		    </el-table-column>
		  </el-table>
		</div>
		<div class="pager" v-if="!isLoading">
			<el-pagination
		      @size-change="handleSizeChange"
		      @current-change="handleCurrentChange"
		      :current-page="currentPage"
		      :page-sizes="[20, 50, 100]"
		      :page-size="limit"
		      layout="total, sizes, prev, pager, next, jumper"
		      :total="count">
		    </el-pagination>
		</div>
		<el-dialog title="更新诊断记录" :visible.sync="dialogFormVisible" :append-to-body="true" :center="true">
		  <el-form :model="form">
		    <el-form-item label="病理号" :label-width="formLabelWidth">
		      <!-- <el-input v-model="form.id" autocomplete="off"></el-input> -->
		      {{ form.pathology }}
		    </el-form-item>
		    <el-form-item label="最新诊断标签" :label-width="formLabelWidth">
		      <el-select v-model="form.diagnosis_label_lastest" placeholder="请选择标签">
		        <el-option :label="item.label_name" :value="item.label_val" v-for="(item,index) in labelList.diagnosis_label" :key="index"></el-option>
		      </el-select>
		    </el-form-item>
		    <el-form-item label="历史诊断标签1" :label-width="formLabelWidth">
		      <el-select v-model="form.his_diagnosis_label1" placeholder="请选择标签">
		        <el-option :label="item.label_name" :value="item.label_val" v-for="(item,index) in labelList.diagnosis_label" :key="index"></el-option>
		      </el-select>
		    </el-form-item>
		    <el-form-item label="历史诊断标签2" :label-width="formLabelWidth">
		      <el-select v-model="form.his_diagnosis_label2" placeholder="请选择标签">
		        <el-option :label="item.label_name" :value="item.label_val" v-for="(item,index) in labelList.diagnosis_label" :key="index"></el-option>
		      </el-select>
		    </el-form-item>
		    <el-form-item label="历史诊断标签3" :label-width="formLabelWidth">
		      <el-select v-model="form.his_diagnosis_label3" placeholder="请选择标签">
		        <el-option :label="item.label_name" :value="item.label_val" v-for="(item,index) in labelList.diagnosis_label" :key="index"></el-option>
		      </el-select>
		    </el-form-item>
		    <el-form-item label="历史诊断标签4" :label-width="formLabelWidth">
		      <el-select v-model="form.his_diagnosis_label4" placeholder="请选择标签">
		        <el-option :label="item.label_name" :value="item.label_val" v-for="(item,index) in labelList.diagnosis_label" :key="index"></el-option>
		      </el-select>
		    </el-form-item>
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="dialogFormVisible = false">取 消</el-button>
		    <el-button type="primary" @click="submit">确 定</el-button>
		  </div>
		</el-dialog>
		<el-dialog
		  title="提示"
		  :visible.sync="DialogVisible2"
		  width="30%"
		  >
		  <a :href="downloadsUrl+'csv'" @click="DialogVisible2 = false">导出为csv文件</a><br/>
		  <a :href="downloadsUrl+'xlsx'" @click="DialogVisible2 = false">导出为excel文件</a>
		</el-dialog>
	</div>
</template>

<script>
	const labelList = require('../../api/labelDataList.js');


	export default({
		name: 'diagnosisTable',
		data() {
			return {
				tableData: [],
				isLoading: true,
				dialogFormVisible: false,
				form: {},
				formLabelWidth: '150px',
				isNetErr: false,
				count: 0,
				currentPage: 1,
				offset: 0,
				limit: 20,
				search_val: '',
				labelList: [],
				downloadsUrl: '',
				DialogVisible2: false,
				uploadUrl: '',
				isUpload: false,
			}
		},
		computed: {
			table_height: function(){
				return this.$store.state.table_height;
			}
		},
		created: function(){
			this.labelList = labelList;
			this.downloadsUrl = 'http://192.168.2.179:8000/api/v1/diagnosis/downloads/?type=';
			this.uploadUrl = this.API + '/api/v1/diagnosis/uploads/';
		},
		mounted: function(){
			var that = this;
			that.getDiagnosis();
		},
		methods: {
			// 获取诊断记录列表
			getDiagnosis: function(url='/api/v1/diagnosis/?limit=20&offset=0'){
				var that = this;
				that.$axios.get(this.API+url).then(res => {
					that.tableData = res.data.results;
					that.isLoading = false;
					that.count = res.data.count;
				}).catch(function(err){
					that.isNetErr = true;
				})
			},
      		// 更新表格某条记录
      		updateRow: function(index, rows){
      			this.form = JSON.parse(JSON.stringify(rows[index]));
      			this.dialogFormVisible = true;
      		},
      		// 分页切换每页条数切换
      		handleSizeChange(val) {
		        this.limit = val;
		        this.handleCurrentChange(this.currentPage);
		    },
		    // 分页切换页码触发事件
		    handleCurrentChange(val) {
		    	this.isLoading = true;
		    	this.currentPage = val;
		        var url = '/api/v1/diagnosis/?limit='+this.limit+'&offset='+(val-1)*this.limit;
		        this.getDiagnosis(url);
		    },
		    // 搜索
		    search: function(){
		    	this.isLoading = true;
		    	if(this.search_val == '')
		    		this.getDiagnosis();
		    	else
		    		this.getDiagnosis('/api/v1/diagnosis/?pathology='+this.search_val);
		    },
		    // 更新记录
		    submit: function(){
		    	this.$axios.patch(this.API+'/api/v1/diagnosis/'+this.form.id+'/',this.form).then(res => {
		    		this.$message({
		    			message: '更新成功！',
		    			type: 'success'
		    		});
		    		if(this.search_val != '')
		    			this.search();
		    		else
		    			this.getDiagnosis('/api/v1/diagnosis/?limit='+this.limit+'&offset='+(this.currentPage-1)*this.limit);
		    		this.dialogFormVisible = false;
		    	}).catch(err => {
		    		this.$message('更新失败，失败原因：'+err);
		    	})
		    },
		    // 上传文件成功回调
		    success_upload: function(res,file,fileList){
		    	this.isUpload = false;
		    	this.$notify({
		    		title: '提示',
		    		message: '诊断信息导入成功！',
		    		duration: 0
		    	});
		    },
		    // 检查文件类型
		    before_upload: function(file){
		    	if(file.type == 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' || file.type == 'application/vnd.ms-excel'){
		    		this.isUpload = true;
		    	}else{
		    		this.$message.error('仅支持导入excel和csv格式文件！');
		    		return false;
		    	}
		    },
		    // 上传失败回调
		    error_upload: function(err,file,fileList){
		    	this.isUpload = false;
		    	this.$notify({
		    		title: '提示',
		    		message: '诊断信息导入失败！',
		    		duration: 0
		    	});
		    },
		    // 导出数据
		    download: function(){
		    	this.DialogVisible2 = true;
		    	
		    }
    	},

	})

</script>

<style scoped="scoped">
	.loading{display: flex;justify-content: center;align-items: center;height: 100%;flex-direction: column;line-height: 50px;}
	.loading-i{font-size: 35px;color:#367dd5;}
	.search-right{display: flex;}
</style>


