<template>
	<div class="index">
		<div class="topHeader">
			<topHeader />
		</div>
		<div class="contain">
			<div class="left">
				<menusList />
			</div>
			<div class="right">
				<indexCon v-if="currTable_id == 0" />
				<casesTable v-else-if="currTable_id == 1"/>
				<imagesTable v-else-if="currTable_id == 2" />	
				<diagnosisTable v-else-if="currTable_id == 3"	/>	
				<checksTable v-else-if="currTable_id == 4" />
				<renamesTable v-else-if="currTable_id == 7" />
				<repeatedTable v-else-if="currTable_id == 8" />
			</div>
			
		</div>
	</div>
</template>

<script>
	import topHeader from "../../template/topHeader/index"
	import indexCon from "../../template/indexCon/index"
	import casesTable from "../../template/casesTable/index"
	import diagnosisTable from "../../template/diagnosisTable/index"
	import imagesTable from "../../template/imagesTable/index"
	import renamesTable from "../../template/renamesTable/index"
	import menusList from '../../template/menusList/index'
	import repeatedTable from '../../template/repeatedTable/index'
	import checksTable from '../../template/checksTable/index'

	export default({
		name: 'index',
		data(){
			return {

			}
		},
		computed:{
			currTable_id: function(){
				return this.$store.state.currTable_id;
			}
		},
		components: {
			indexCon,
			topHeader,
			casesTable,
			diagnosisTable,
			imagesTable,
			menusList,
			renamesTable,
			repeatedTable,
			checksTable,
		},
		created: function(){
			var that = this;
			
		}
	})
</script>

<style scoped="scoped">
	/*.index{position: fixed;width: 100%;height: 100%;}*/
	.contain{height: 100%;display: flex;margin-top: 20px;}
	.left{width: 200px;}
	.right{width: 100%;border: 1px solid #e6e6e6;margin: 0 20px;padding: 20px;margin-bottom: 20px;}
</style>


