<template>
	<div class="imagesTable dataTable">
		<div v-if="isLoading" class="loading">
			<i class="loading-i fa fa-spinner fa-pulse"></i>
			<span v-if="isNetErr">获取数据失败，请检查网络是否异常</span>
			<span v-else>正在加载中</span>
		</div>
		<div v-else>
			<div class="search">
				<div>
					<el-input type="text" v-model="search_val" placeholder="请输入病理号" style="width: 200px;"></el-input>
					<el-button type="primary" @click="search"><i class="fa fa-search"></i>查询</el-button>
					<el-button type="primary" @click="update">{{isUpdate?'正在更新中...':'更新'}}<i class="fa fa-spinner fa-pulse" v-if="isUpdate"></i></el-button>					
				</div>
				<div class="search-right">
					<el-button type="primary" @click="download">导出</el-button>
				</div>
			</div>
			<el-table
			    :data="tableData"
			    style="width: 100%"
			    :max-height="table_height"
			    >
		    <el-table-column
		      type="selection"
		      width="55">
		    </el-table-column>
		    <el-table-column
		      fixed
		      prop="pathology"
		      label="病理号"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="file_name"
		      label="文件名"
		      :show-overflow-tooltip="true"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="resolution"
		      label="分辨率"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="storage_path"
		      label="存储路径"
		      :show-overflow-tooltip="true"
		      >
		      <template slot-scope="scope">
				<span class="storage_path" :data-clipboard-text="scope.row.storage_path | images_path" @click="copy">{{ scope.row.storage_path | images_path }}</span>
				<span>（单击可复制）</span>
		      </template>
		    </el-table-column>
		    <el-table-column
		      prop="waveplate_source"
		      label="片源"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="is_learn"
		      label="是否学习"
		      >
		      	<template slot-scope="scope">
					{{ scope.row.is_learn?'true':'false'}}
		      	</template>
		    </el-table-column>
		    <el-table-column
		      prop="diagnosis_label_doctor"
		      label="医生诊断标签"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="diagnosis_label_zhu"
		      label="朱博士诊断"
		      :show-overflow-tooltip="true"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="making_way"
		      label="切片制式"
		      :show-overflow-tooltip="true"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="scan_time"
		      label="扫描时间"
		      :show-overflow-tooltip="true"
		      >
		    </el-table-column>
		    <el-table-column
		      fixed="right"
		      label="操作"
		      >
		      <template slot-scope="scope">
		        <el-button
		          @click.native.prevent="updateRow(scope.$index, tableData)"
		          type="text"
		          size="small">
		          更新
		        </el-button>
		        <el-button
		          @click.native.prevent="deleteRow(scope.$index, tableData)"
		          type="text"
		          size="small"
				  v-if="search_val!=''"
		          >
		          删除
		        </el-button>
		      </template>
		    </el-table-column>
		  </el-table>
		</div>
		<el-dialog title="更新大图记录" :visible.sync="dialogFormVisible" :append-to-body="true" :center="true">
		  <el-form :model="form">
		    <el-form-item label="病理号" :label-width="formLabelWidth">
		    	<el-input v-model="form.pathology" autocomplete="off"></el-input>
		    </el-form-item>
			<el-form-item label="文件名" :label-width="formLabelWidth">
		    	<el-input v-model="form.file_name" autocomplete="off"></el-input>
		    </el-form-item>
		    
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="dialogFormVisible = false">取 消</el-button>
		    <el-button type="primary" @click="submit">确 定</el-button>
		  </div>
		</el-dialog>
		<div class="pager" v-if="!isLoading">
			<el-pagination
		      @size-change="handleSizeChange"
		      @current-change="handleCurrentChange"
		      :current-page="currentPage"
		      :page-sizes="[20, 50, 100]"
		      :page-size="limit"
		      layout="total, sizes, prev, pager, next, jumper"
		      :total="count">
		    </el-pagination>
		</div>
		<el-dialog
		  title="提示"
		  :visible.sync="DialogVisible2"
		  width="30%"
		  >
		  <a :href="downloadsUrl+'csv'" @click="DialogVisible2 = false">导出为csv文件</a><br/>
		  <a :href="downloadsUrl+'xlsx'" @click="DialogVisible2 = false">导出为excel文件</a>
		</el-dialog>
	</div>
</template>

<script>
	import Clipboard from 'clipboard';	//引入复制到粘贴板的类库
	const labelList = require('../../api/labelDataList.js');

	export default({
		name: 'imagesTable',
		data() {
			return {
				tableData: [],
				isLoading: true,
				isNetErr: false,
				count: 0,
				currentPage: 1,
				offset: 0,
				limit: 20,
				dialogFormVisible: false,
				form: {},
				formLabelWidth: '150px',
				search_val: '',
				labelList: [],
				downloadsUrl: '',
				DialogVisible2: false,
				uploadUrl: '',
				isUpdate: false,
			}
		},
		computed: {
			table_height: function(){
				return this.$store.state.table_height;
			},
			repeated_val: function(){
				return this.$store.state.repeated_val;
			}
		},
		filters: {
			images_path: function(n){
				return n.split('=')[2];
			}
		},
		created: function(){
			this.labelList = labelList;
			this.downloadsUrl = this.API+'/api/v1/images/downloads/?type=';
		},
		mounted: function(){
			var that = this;
			if(that.$store.state.repeated_val != ''){
				that.search_val = that.$store.state.repeated_val;
				that.search();
			}
			else
				that.getImages();
		},
		destroyed: function(){
			this.$store.commit('setRepeated_val','');
		},
		methods: {
			// 获取大图记录列表
			getImages(url='/api/v1/images/?limit=20&offset=0'){
				var that = this;
				that.$axios.get(this.API+url).then(res => {
					that.tableData = res.data.results;
					that.isLoading = false;
					that.count = res.data.count;
				}).catch(err => {
					that.isNetErr = true;
				});
			},
			// 删除某条记录
			deleteRow: function(index, rows){
				this.$confirm('此操作将永久删除该文件, 是否继续?', '警告', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		          this.$axios.delete(this.API+'/api/v1/images/'+rows[index].id+'/').then(res => {
		          	this.search();
					this.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
		          }).catch(err => {
		          	this.$message('病例删除接口获取失败！')
		          });
		          
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: '已取消删除'
		          });          
		        });
			},
      		// 更新表格某条记录
      		updateRow: function(index, rows){
      			this.form = JSON.parse(JSON.stringify(rows[index]));
      			this.dialogFormVisible = true;
      		},
      		// 分页切换每页条数切换
      		handleSizeChange(val) {
		        this.limit = val;
		        this.handleCurrentChange(this.currentPage);
		    },
		    // 分页切换页码触发事件
		    handleCurrentChange(val) {
		    	this.isLoading = true;
		    	this.currentPage = val;
		        var url = '/api/v1/images/?limit='+this.limit+'&offset='+(val-1)*this.limit;
		        this.getImages(url);
		    },
		    // 搜索
		    search: function(){
		    	this.isLoading = true;
		    	if(this.search_val == '')
		    		this.getImages();
		    	else
		    		this.getImages('/api/v1/images/?pathology='+this.search_val);
		    },
		    // 确认更新
		    submit: function(){
		    	this.$axios.patch(this.API+'/api/v1/images/'+this.form.id+'/',this.form).then(res => {
		    		this.$message({
		    			message: '更新成功！',
		    			type: 'success'
		    		});
		    		this.dialogFormVisible = false;
		    		if(this.search_val != '')
		    			this.search();
		    		else
		    			this.getImages('/api/v1/images/?limit='+this.limit+'&offset='+(this.currentPage-1)*this.limit);	    		
		    	}).catch(err => {
		    		
		    	});
		    },
		    // 导出数据
		    download: function(){
		    	this.DialogVisible2 = true;		    	
		    },
		    // 复制图像存储路径
		    copy: function(val){
		    	const copy = new Clipboard('.storage_path');
		    	this.$message({
		        	message: '复制成功！',
		        	type: 'success'
		        })
		    },
		    // 从服务器更新所有数据
		    update: function(){
		    	if(this.isUpdate) return;
		    	var update_type = 'db';
		    	this.$confirm('更新过程可能需要几分钟，请确定是否要更新','提示',{
		    		confirmButtonText: '确认',
		    		cancelButtonText: '取消',
		    		type: 'warning'
		    	}).then(() => {
		    		this.isUpdate = true;
		          this.$axios.post(this.API+'/api/v1/images/updates/',{update_type: update_type},{
                    responseType: 'json',
                    
                }).then(res => {
		          	this.isUpdate = false;
					this.$notify({
			          title: '提示',
			          message: res.data.msg+'(一共耗时：'+res.data.cost_time+'秒）',
			          duration: 0
			        });
		          }).catch(err => {
		          	this.isUpdate = false;
		          	this.$notify({
		          		title: '提示',
		          		message: '大图信息更新失败',
		          		duration: 0
		          	})
		          });
		          
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: '已取消更新'
		          });          
		        });
		    }
    	},

	})

</script>

<style scoped="scoped">
	.loading{display: flex;justify-content: center;align-items: center;height: 100%;flex-direction: column;line-height: 50px;}
	.loading-i{font-size: 35px;color:#367dd5;}
	.storage_path{cursor: pointer;}
	.search-right{display: flex;}
</style>


