import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)


const store = new Vuex.Store({
	state: {
		currTable_id: sessionStorage.getItem('state') ? JSON.parse(sessionStorage.getItem('state')) : 0,
		table_height: window.innerHeight-260,
		repeated_val: ''
	},
	mutations: {
		setCurrTable_id(state,id) {
			state.currTable_id = id;
		},
		setRepeated_val(state,val) {
			state.repeated_val = val;
		},
		setTable_height(state,val){
			state.table_height = val;
		}
	}
})

export default store;


