<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted: function(){
    // 监听页面刷新
     window.addEventListener('unload', this.saveState);
     // 监听页面刷新
     window.addEventListener('resize', this.setHeight);
  },
  methods: {
    // 页面刷新时将state状态存入到sessionStorage
    saveState: function(){
      sessionStorage.setItem("state",JSON.stringify(this.$store.state.currTable_id));
    },
    // 页面窗口改变时修改表格高度
    setHeight: function(){
      this.$store.commit('setTable_height',window.innerHeight-260>0?window.innerHeight-260:400)
    }
  }
}
</script>

<style>
  @import "./assets/css/style.css";
  @import "./assets/css/font/IconFonts.css";
</style>



