<template>
	<div class="repeatedTable dataTable">
		<el-tabs v-model="activeName" @tab-click="handleClick">
		    <el-tab-pane label="病例信息" name="0"></el-tab-pane>
		    <el-tab-pane label="大图信息" name="1"></el-tab-pane>
		</el-tabs>
		<div class="loading" v-if="isLoading">
			<i class="loading-i fa fa-spinner fa-pulse"></i>
			<span v-if="isNetErr">获取数据失败，请检查网络是否异常</span>
			<span v-else>正在加载中</span>
		</div>
		<div v-else>
			<el-table
			    :data="tableData"
			    style="width: 100%"
			    :max-height="table_height">
		    <el-table-column
		      fixed
		      prop="pathology"
		      label="病理号"
		      >
		      <template slot-scope="scope">
		      	<span style="cursor: pointer;" @click="toOther(scope.row.pathology)">{{ scope.row.pathology }}</span>
		      </template>
		    </el-table-column>
		    <el-table-column
		      v-if="activeName==1"
		      prop=""
		      label="分辨率"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="dup_count"
		      label="重复次数"
		      >
		    </el-table-column>
		  </el-table>
		  <div class="pager" v-if="!isLoading">
			<el-pagination
		      @size-change="handleSizeChange"
		      @current-change="handleCurrentChange"
		      :current-page="currentPage"
		      :page-sizes="[20, 50, 100]"
		      :page-size="limit"
		      layout="total, sizes, prev, pager, next, jumper"
		      :total="count">
		    </el-pagination>
		</div>
		</div>
	</div>
</template>

<script>

	export default({
		name: 'repeatedTable',
		data() {
			return {
				activeName: 0,
				tableData: [],
				isLoading: true,
				isNetErr: false,
				count: 0,
				currentPage: 1,
				offset: 0,
				limit: 20,
			}
		},
		computed: {
			table_height: function(){
				return this.$store.state.table_height;
			}
		},
		mounted: function(){
			var that = this;
			if(that.$store.state.repeated_val != ''){
				that.search_val = that.$store.state.repeated_val;
				that.search();
			}else
				that.getCases();
		},
	    methods: {
	    	// 获取病例文件重复数据
	    	getCases: function(url="/api/v1/cases/duplicates/?limit=20&offset=0"){
	    		this.$axios.get(this.API+url).then(res => {
					this.isLoading = false;
					this.count = res.data.count;
					this.tableData = res.data.results;
				}).catch(err => {
					this.isNetErr = true;
				});
	    	},
	    	// 获取大图文件重复数据
	    	getImages: function(){
	    		this.$axios.get(this.API+'/api/v1/images/duplicates/?limit=20&offset=0').then(res => {
					this.isLoading = false;
					this.count = res.data.count;
					this.tableData = res.data.results;
				}).catch(err => {
					this.isNetErr = true;
				});
	    	},
	    	// 监听TAB切换
	        handleClick(tab, event) {
	        	console.log(this.activeName);
	        	if(this.activeName == 0)
	        		this.getCases();
	        	else
	        		this.getImages();

	        },
	        // 跳转至病例或大图信息
	        toOther: function(val){
	        	if(this.activeName == 0){
	        		this.$store.commit('setCurrTable_id',1)
	        	}else{
	        		this.$store.commit('setCurrTable_id',2)
	        	}
	        	this.$store.commit('setRepeated_val',val);

	        },
	        // 分页切换每页条数切换
      		handleSizeChange(val) {
		        this.limit = val;
		        this.handleCurrentChange(this.currentPage);
		    },
		    // 分页切换页码触发事件
		    handleCurrentChange(val) {
		    	var a = '';
		    	this.isLoading = true;
		    	this.currentPage = val;
		    	if(this.activeName == 0)	
		    		a = 'cases';
		    	else
		    		b = 'images';
		        var url = '/api/v1/'+a+'/duplicates/?limit='+this.limit+'&offset='+(val-1)*this.limit;
		        this.getCases(url);
		    },
	    }

	});

</script>

<style scoped="scoped">
	.loading{display: flex;justify-content: center;align-items: center;height: 100%;flex-direction: column;line-height: 50px;}
	.loading-i{font-size: 35px;color:#367dd5;}		

</style>


