<template>
	<div class="menusList">
		<div class="item" v-for="item in toolbarList" @click="toggle(item.id)">
			<span :class="item.id==currTable_id?'selected':''">{{item.name}}</span>
		</div>
	</div>
</template>

<script>
	export default({
		name: 'menusList',
		data() {
			return {
				toolbarList: [
					{
						id: 0,
						name: '首页'
					},{
						id: 1,
						name: '病例信息'
					},{
						id: 2,
						name: '大图信息'
					},{
						id: 3,
						name: '朱博士诊断'
					},{
						id: 4,
						name: '审核数据'
					},{
						id: 5,
						name: '训练数据'
					},{
						id: 6,
						name: '算法版本'
					},{
						id: 7,
						name: '文件更名记录'
					},{
						id: 8,
						name: '文件名重复'
					},
				]
			}
		},
		computed: {
			currTable_id: function(){
				return this.$store.state.currTable_id;
			}
		},
		methods: {
			toggle: function(id){
				this.$store.commit("setCurrTable_id",id)
			}
		}
	});
</script>

<style scoped="scoped">
	.menusList{}
	.item{text-align: center;padding: 20px;background: #367dd5;color: #fff;cursor:pointer;}
	.item span{position: relative;}
	.selected:after{position: absolute;width: 100%;height: 5px;background: #fff;content: '';bottom: -10px;left: 0}
	.item span:hover{font-weight: bold;opacity: 0.6}
</style>


