<template>
	<div class="indexCon">
		<div class="top">
			<div class="information" @click="toRenames"><i class="fa fa-comment"></i><span class="repeatCount" v-if="repeatCount!=''">{{ repeatCount }}</span></div>
			<div class="top-left">
				<div class="train">
					<div class="middle">训练结果：</div>
					<div class="result">这是训练结果</div>
				</div>
				<div class="total">
					<p>病例总数：<span class="big">{{ dataList.case_count }}</span></p>
					<p>大图总数：<span class="big">{{ dataList.image_count }}</span></p>
				</div>
				<div class="echarts-a" id="echarts-a"></div>
			</div>
			<div class="top-right">
				<div class="right-title">诊断类别：</div>
				<div id="echarts-b" class="echarts-b"></div>
				<div class="right-count">
					<div class="count-item" v-for="item in dataList.resolution_count">
						<span>{{ item.resolution=='20X'?'20倍数字切片':'40倍数字切片' }}：</span>
						<span>{{ item.resolution_count }}</span>
						<span style="float:right">{{ item.label_prop }}</span>
					</div>
				</div>
			</div>
		</div>
		<div class="bottom">
			<div class="bottom-title"><span class="middle">当前算法版本：</span>XX4562</div>
			<div class="bottom-items">
				<span>训练数据版本： XXXXXX</span>
				<span>审核数据版本： XXXXXX</span>
				<span>训练数量： XXXXXX</span>
				<span>描述+增强策略： XXXXXX</span>
				<span>时间： 2019年1月8日</span>

			</div>
		</div>
	</div>
</template>

<script>
	const filterList = ['NORMAL','ASCUS','LSIL','ASC-H','HSIL','SCC','AGC1','AGC2','AGC3','ADC','TRI','FUNGI','ACTINO','HSV','CC','EC'];
	const AGCList = ['AGC1','AGC2','AGC3'];
	var echarts = require('echarts');
	export default({
		name: 'indexCon',
		data() {
			return {
				dataList: {},
				repeatCount: '',
			}
		},
		created: function(){
			var that = this;
			that.$axios.get(that.API+'/api/v1/images/statistics/').then(res => {
				that.dataList = res.data;
				that.handleData();
			}).catch(function(err){

			});
			that.$axios.get(that.API+'/api/v1/cases/duplicates/?limit=10&offset=10').then(res => {
				if(res.data.count > 0){
					that.repeatCount = '!'
				}
			}).catch(err => {
				that.$message("病例文件重复接口获取失败！")
			});
			that.$axios.get(that.API+'/api/v1/images/duplicates/?limit=10&offset=10').then(res => {
				if(res.data.count > 0){
					that.repeatCount = '!'
				}
			}).catch(err => {
				that.$message("大图文件重复接口获取失败！")
			});
		},
		mounted: function(){
		
		},
		methods: {
			// 绘制柱形图和饼图
			handleData: function(){
				var that = this;
				// 基于准备好的dom，初始化echarts实例
				var myChart = echarts.init(document.getElementById('echarts-a'));
				var data_a_1 = []
				 ,data_a_2 = [];
				for(var i = 0; i < that.dataList.waveplate_source_count.length; i++){
					data_a_1.push(that.dataList.waveplate_source_count[i].waveplate_source);
					data_a_2.push(that.dataList.waveplate_source_count[i].waveplate_source_count);
				}
				// 绘制图表
				myChart.setOption({
				    title: {
				        text: '切片来源'
				    },
				    tooltip: {
				    	trigger: 'item',
        				formatter: function(v) {
        					return v.name + ": " + v.data + '(' + (v.data/that.dataList.case_count*100).toFixed(3) + '%)';
        				}
				    },
				    xAxis: {
				        data: data_a_1
				    },
				    yAxis: {},
				    series: [{
				        name: '数量',
				        type: 'bar',
				        data: data_a_2,
				        itemStyle: { // 柱子样式
					      normal: {					        
					        label: {
					          show: true, // 显示文本
					          position: 'top', // 数据值位置
					          formatter: function(v) {
				                    var val = v.data;
				                    return val + '\n('+ (val/that.dataList.case_count*100).toFixed(3) +'%)';
				                },
					          textStyle: {
					            color: '#000'
					          }
					        }
					      }
					    }
				    }]
				});

				var data_b_1 = []
				 ,data_b_2 = []
				 ,AGC_count=0;
				for(var i=0;i<that.dataList.diag_label_count.length;i++){
					var count = that.dataList.diag_label_count[i].diagnosis_label_count;
					var filter = that.dataList.diag_label_count[i].diagnosis_label_doctor_filter;
					let a = {
						value: count,
						name: filter
					}
					if(filterList.indexOf(filter) != -1 ){
						if(AGCList.indexOf(filter) != -1){
							AGC_count += count;
							continue;
						}
						data_b_1.push(that.dataList.diag_label_count[i].diagnosis_label_doctor_filter)
						data_b_2.push(a);
					}
				}
				data_b_1.push('AGC');
				data_b_2.push({value: AGC_count,name: 'AGC'});

				

				// 饼状图
				var myChart2 = echarts.init(document.getElementById('echarts-b'));
				myChart2.setOption({
				    title: {
				        text: '',
				        left: 'center'
				    },
				    tooltip : {
				        trigger: 'item',
				        formatter: function(v){
				        	return v.data.name + ': '+v.data.value+'('+(v.data.value/that.dataList.case_count*100).toFixed(3)+'%)';
				        }
				    },
				    legend: {
				        bottom: 10,
				        left: 'center',
				        data: data_b_1
				    },
				    series : [
				        {
				        	name: '',
				            type: 'pie',
				            radius : '65%',
				            center: ['50%', '50%'],
				            selectedMode: 'single',
				            data: data_b_2,
				            itemStyle: {
				                emphasis: {
				                    shadowBlur: 10,
				                    shadowOffsetX: 0,
				                    shadowColor: 'rgba(0, 0, 0, 0.5)'
				                }
				            }
				        }
				    ]
				});
			},
			// 跳转至文件名重复模块
			toRenames: function(){
				this.$store.commit("setCurrTable_id",8)
			}
		}

	})
</script>

<style scoped="scoped">
	.top{display: flex;position: relative;}
	.information{position: absolute;right: 0;font-size: 35px;cursor: pointer;}
	.repeatCount{color: #fff;background: red;border-radius: 50%;width: 20px;text-align: center;height: 20px;font-size: 14px;position: absolute;}
	.top-left{width: 50%;display: flex;flex-direction: column;align-items: center;}
	.top-left p{font-weight: bold;font-size: 25px;}
	.big{font-size: 70px;}
	.top-right{width: 50%;}
	.right-title{font-weight: bold;font-size: 25px}
	.echarts-a{border-top: 1px dashed;padding-top: 20px;margin-top: 20px;width: 600px;height:400px;}
	.echarts-b{width: 100%;height:600px;display: flex;justify-content: center;border-bottom: 1px dashed;}
	.right-count{font-weight: bold;font-size: 25px;line-height: 40px;margin-top: 20px;padding-top: 20px;width: 50%;}
	.bottom{padding: 0 40px }
	.bottom-title{font-size: 25px;}
	.middle{font-weight: bold;font-size: 25px;}
	.bottom-items{display: flex;margin-top: 20px;}
	.bottom-items span{margin-right: 40px;}
	.train{width: 90%;}
	.result{padding: 10px;height: 150px;background: #fff;}
</style>


