<template>
	<div class="checksTable dataTable">
		<div class="loading" v-if="isLoading">
			<i class="loading-i fa fa-spinner fa-pulse"></i>
			<span v-if="isNetErr">获取数据失败，请检查网络是否异常</span>
			<span v-else>正在加载中</span>
		</div>
		<div v-else>
			<div class="search">
				<div>
					<el-input type="text" v-model="search_val" placeholder="请输入病理号" style="width: 200px;"></el-input>
					<el-button type="primary" @click="search"><i class="fa fa-search"></i>查询</el-button>
					<el-button type="primary" @click="update">{{isUpdate?'正在更新中...':'更新'}}<i class="fa fa-spinner fa-pulse" v-if="isUpdate"></i></el-button>
				</div>
				<div class="search-right">
					<el-button type="primary" @click="download">导出</el-button>
				</div>
			</div>
			<el-table
			    :data="tableData"
			    style="width: 100%"
			    :max-height="table_height">
		    <el-table-column
		      type="selection"
		      width="55">
		    </el-table-column>
		    <el-table-column
		      fixed
		      prop="id"
		      label="id"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="check_version_number"
		      label="审核版本号"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="cells_number"
		      label="细胞数量"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="class_number"
		      label="分类数量"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="classify"
		      label="分类"
		      :show-overflow-tooltip="true"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="check_date"
		      label="采样/检查时间"
		      :show-overflow-tooltip="true"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="storage_path"
		      label="存储路径"
		      :show-overflow-tooltip="true"
		      >
		      <template slot-scope="scope">
				<span class="storage_path" :data-clipboard-text="scope.row.storage_path" @click="copy">{{ scope.row.storage_path }}</span>
				<span>（单击可复制）</span>
		      </template>
		  </el-table-column>
		      <el-table-column
		      prop="image_format"
		      label="图像格式"
		      >
		  </el-table-column>
		      <el-table-column
		      prop="source"
		      label="数据来源"
		      :show-overflow-tooltip="true"
		      >
		    </el-table-column>
		    <el-table-column
		      fixed="right"
		      label="操作"
		      >
		      <template slot-scope="scope">
		        <el-button
		          @click.native.prevent="updateRow(scope.$index, tableData)"
		          type="text"
		          size="small">
		          更新
		        </el-button>
		      </template>
		    </el-table-column>
		  </el-table>
		</div>
		<div class="pager" v-if="!isLoading">
			<el-pagination
		      @size-change="handleSizeChange"
		      @current-change="handleCurrentChange"
		      :current-page="currentPage"
		      :page-sizes="[20, 50, 100]"
		      :page-size="limit"
		      layout="total, sizes, prev, pager, next, jumper"
		      :total="count">
		    </el-pagination>
		</div>
		<el-dialog title="更新审核记录" :visible.sync="dialogFormVisible" :append-to-body="true" :center="true">		
		  <el-form :model="form">
		  	<el-form-item label="id" :label-width="formLabelWidth">
		      {{ form.id }}
		    </el-form-item>
		    <el-form-item label="数据来源" :label-width="formLabelWidth">
		      <el-input v-model="form.source" autocomplete="off"></el-input>
		    </el-form-item>    
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="dialogFormVisible = false">取 消</el-button>
		    <el-button type="primary" @click="submit">确 定</el-button>
		  </div>
		</el-dialog>
		<el-dialog
		  title="提示"
		  :visible.sync="DialogVisible2"
		  width="30%"
		  >
		  <a :href="downloadsUrl+'csv'" @click="DialogVisible2 = false">导出为csv文件</a><br/>
		  <a :href="downloadsUrl+'xlsx'" @click="DialogVisible2 = false">导出为excel文件</a>
		</el-dialog>
	</div>
</template>

<script>
	import Clipboard from 'clipboard';	//引入复制到粘贴板的类库
	const labelList = require('../../api/labelDataList.js');
	var loading;

	export default({
		name: 'checksTable',
		data() {
			return {
				tableData: [],
				isLoading: true,
				isNetErr: false,
				form: {},
				dialogFormVisible: false,
				formLabelWidth: '150px',
				count: 0,
				currentPage: 1,
				offset: 0,
				limit: 20,
				search_val: '',
				labelList: [],
				downloadsUrl: '',
				DialogVisible2: false,
				uploadUrl: '',
				isUpdate: false,
			}
		},
		computed: {
			table_height: function(){
				return this.$store.state.table_height;
			},
			repeated_val: function(){
				return this.$store.state.repeated_val;
			}			
		},
		created: function(){
			this.labelList = labelList;
			this.downloadsUrl = this.API+'/api/v1/checks/details/downloads/?type=';
			this.uploadUrl = this.API + '/api/v1/checks/uploads/';
		},
		mounted: function(){
			var that = this;
			if(that.$store.state.repeated_val != ''){
				that.search_val = that.$store.state.repeated_val;
				that.search();
			}
			else
				that.getChecks();
		},
		destroyed: function(){
			this.$store.commit('setRepeated_val','');
		},
		methods: {
			// 获取病例记录列表
			getChecks: function(url='/api/v1/checks/?limit=20&offset=0'){
				var that = this;
				that.$axios.get(this.API+url).then(res => {
					that.tableData = res.data.results;
					that.isLoading = false;
					that.count = res.data.count;
				}).catch(err => {
					that.isNetErr = true;
				});
			},
			// 删除某条记录
			deleteRow: function(index, rows){
				this.$confirm('此操作将永久删除该文件, 是否继续?', '警告', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		          this.$axios.delete(this.API+'/api/v1/checks/'+rows[index].id+'/').then(res => {
					this.$message({
		            	type: 'success',
		            	message: '删除成功!'
		          	});
		          }).catch(err => {
		          	this.$message('删除接口获取失败！')
		          });
		          
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: '已取消删除'
		          });          
		        });
			},
      		// 更新表格某条记录
      		updateRow: function(index, rows){
      			this.form = JSON.parse(JSON.stringify(rows[index]));
      			this.dialogFormVisible = true;
      		},
      		// 确认更新记录
      		submit: function(){
      			this.$axios.patch(this.API+'/api/v1/checks/'+this.form.id+'/',this.form).then(res => {
		    		this.$message({
		    			message: '更新成功！',
		    			type: 'success'
		    		});
		    		this.dialogFormVisible = false;
		    		if(this.search_val != '')
		    			this.search();
		    		else
		    			this.getChecks('/api/v1/checks/?limit='+this.limit+'&offset='+(this.currentPage-1)*this.limit);
		    		
		    	}).catch(err => {
		    		
		    	});
      		},
      		// 分页切换每页条数切换
      		handleSizeChange(val) {
		        this.limit = val;
		        this.handleCurrentChange(this.currentPage);
		    },
		    // 分页切换页码触发事件
		    handleCurrentChange(val) {
		    	this.isLoading = true;
		    	this.currentPage = val;
		        var url = '/api/v1/checks/?limit='+this.limit+'&offset='+(val-1)*this.limit;
		        this.getChecks(url);
		    },
		    // 搜索
		    search: function(){
		    	this.isLoading = true;
		    	if(this.search_val == '')
		    		this.getChecks();
		    	else
		    		this.getChecks('/api/v1/checks/?id='+this.search_val);
		    },	    
		    // 导出数据
		    download: function(){
		    	var types = 'csv';
		    	this.DialogVisible2 = true;
		    	
		    },
		    // 复制图像存储路径
		    copy: function(val){
		    	const copy = new Clipboard('.storage_path');
		    	this.$message({
		        	message: '复制成功！',
		        	type: 'success'
		        })
		    },
		    // 从服务器更新所有数据
		    update: function(){
		    	if(this.isUpdate) return;
		    	var update_type = '';
		    	this.$confirm('更新过程可能需要几分钟，请确定是否要更新','提示',{
		    		confirmButtonText: '确认',
		    		cancelButtonText: '取消',
		    		type: 'warning'
		    	}).then(() => {
			    	this.$confirm('请选择更新哪个数据','提示',{
			    		confirmButtonText: '审核数据记录列表',
			    		cancelButtonText: '详情信息',
			    		type: 'warning'
			    	}).then(() => {
			    		update_type = 'sample';
			    		this.isUpdate = true;
			    		this.$axios.post(this.API+'/api/v1/checks/updates/',{update_type: update_type}).then(res => {
				          	this.isUpdate = false;
				          	this.$notify({
					          title: '提示',
					          message: res.data.msg+'(一共耗时：'+res.data.cost_time+'秒）',
					          duration: 0
					        });
				          }).catch(err => {
				          	this.$notify({
				          		title: '提示',
				          		message: '审核数据版本更新失败',
				          		duration: 0
				          	});
				          });			          
			        }).catch(() => {
			          update_type = 'detail'; 
			          this.isUpdate = true; 
			          this.$axios.post(this.API+'/api/v1/checks/updates/',{update_type: update_type}).then(res => {
			          	this.isUpdate = false;
			          	this.$notify({
				          title: '提示',
				          message: res.data.msg+'(一共耗时：'+res.data.cost_time+'秒）',
				          duration: 0
				        });
			          }).catch(err => {
			          	this.$notify({
			          		title: '提示',
			          		message: '审核数据版本更新失败',
			          		duration: 0
			          	});
			          });       
			        });
		    				          
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: '已取消更新'
		          });          
		        });
		    }

    	},

	})

</script>

<style scoped="scoped">
	.loading{display: flex;justify-content: center;align-items: center;height: 100%;flex-direction: column;line-height: 50px;}
	.loading-i{font-size: 35px;color:#367dd5;}
	.storage_path{cursor: pointer;}
	.search-right{display: flex;}
</style>


